* Navs.................Site navigations.\n * Sections.............Larger components of pages.\n * Forms................Specific form styling.\n *\n * TEXT\n * Text.................Various text-specific class definitions.\n *\n * PAGE STRUCTURE\n * Article..............Post-type pages with styled text.\n * Footer...............The main page footer.\n * Header...............The main page header.\n * Main.................Content area styles.\n *\n * MODIFIERS\n * Animations...........Animation and transition effects.\n * Colors...............Text and background colors.\n * Display..............Show and hide and breakpoint visibility rules.\n * Spacings.............Padding and margins in classes.\n *\n * TRUMPS\n * Helper Classes.......Helper classes loaded last in the cascade.\n */\n/* ------------------------------------ *\\\n    $SETTINGS\n\\* ------------------------------------ */\n/* ------------------------------------ *\\\n    $MIXINS\n\\* ------------------------------------ */\n/**\n * Convert px to rem.\n *\n * @param int $size\n *   Size in px unit.\n * @return string\n *   Returns px unit converted to rem.\n */\n/**\n * Standard paragraph\n */\n/* ------------------------------------ *\\\n    $VARIABLES\n\\* ------------------------------------ */\n/**\n * Grid & Baseline Setup\n */\n/**\n  * Theme Colors\n  */\n/**\n * Neutral Colors\n */\n/**\n * Default Colors\n */\n/**\n * Style Colors\n */\n/**\n * Typography\n */\n/**\n * Icons\n */\n/**\n * Common Breakpoints\n */\n/**\n * Border Styles\n */\n/**\n * Default Spacing/Padding\n */\n/**\n * Native Custom Properties\n */\n:root {\n  --font-size-xs: 12px;\n  --font-size-s: 14px;\n  --font-size-m: 16px;\n  --font-size-l: 20px;\n  --font-size-xl: 24px;\n  --font-size-xxl: 100px; }\n\n@media screen and (min-width: 500px) {\n  :root {\n    --font-size-xs: 14px;\n    --font-size-s: 16px;\n    --font-size-m: 18px;\n    --font-size-l: 22px;\n    --font-size-xl: 30px;\n    --font-size-xxl: 125px; } }\n\n@media screen and (min-width: 1100px) {\n  :root {\n    --font-size-xs: 15px;\n    --font-size-s: 19px;\n    --font-size-m: 20px;\n    --font-size-l: 24px;\n    --font-size-xl: 36px;\n    --font-size-xxl: 150px; } }\n\n/* ------------------------------------ *\\\n    $TOOLS\n\\* ------------------------------------ */\n/* ------------------------------------ *\\\n    $MEDIA QUERY TESTS\n\\* ------------------------------------ */\n/* ------------------------------------ *\\\n    $GENERIC\n\\* ------------------------------------ */\n/* ------------------------------------ *\\\n    $RESET\n\\* ------------------------------------ */\n/* Border-Box http:/paulirish.com/2012/box-sizing-border-box-ftw/ */\n* {\n  -moz-box-sizing: border-box;\n  -webkit-box-sizing: border-box;\n  box-sizing: border-box; }\n\nbody {\n  margin: 0;\n  padding: 0; }\n\nblockquote,\nbody,\ndiv,\nfigure,\nfooter,\nform,\nh1,\nh2,\nh3,\nh4,\nh5,\nh6,\nheader,\nhtml,\niframe,\nlabel,\nlegend,\nli,\nnav,\nobject,\nol,\np,\nsection,\ntable,\nul {\n  margin: 0;\n  padding: 0; }\n\narticle,\nfigure,\nfooter,\nheader,\nhgroup,\nnav,\nsection {\n  display: block; }\n\naddress {\n  font-style: normal; }\n\n/* ------------------------------------ *\\\n    $BASE\n\\* ------------------------------------ */\n/* ------------------------------------ *\\\n    $FONTS\n\\* ------------------------------------ */\n@font-face {\n  font-family: 'signature_collection_alt';\n  src: url(\"../fonts/signature-collection-alt-webfont.woff2\") format(\"woff2\"), url(\"../fonts/signature-collection-alt-webfont.woff\") format(\"woff\");\n  font-weight: normal;\n  font-style: normal; }\n\n@font-face {\n  font-family: 'silver_south_serif';\n  src: url(\"../fonts/silver-south-serif-webfont.woff2\") format(\"woff2\"), url(\"../fonts/silver-south-serif-webfont.woff\") format(\"woff\");\n  font-weight: normal;\n  font-style: normal; }\n\n@font-face {\n  font-family: 'nexa_book';\n  src: url(\"../fonts/nexa-book-webfont.woff2\") format(\"woff2\"), url(\"../fonts/nexa-book-webfont.woff\") format(\"woff\");\n  font-weight: normal;\n  font-style: normal; }\n\n@font-face {\n  font-family: 'nexa_book_italic';\n  src: url(\"../fonts/nexa-book-italic-webfont.woff2\") format(\"woff2\"), url(\"../fonts/nexa-book-italic-webfont.woff\") format(\"woff\");\n  font-weight: normal;\n  font-style: normal; }\n\n@font-face {\n  font-family: 'nexa_bold';\n  src: url(\"../fonts/nexa-bold-webfont.woff2\") format(\"woff2\"), url(\"../fonts/nexa-bold-webfont.woff\") format(\"woff\");\n  font-weight: normal;\n  font-style: normal; }\n\n@font-face {\n  font-family: 'nexa_heavy';\n  src: url(\"../fonts/nexa-heavy-webfont.woff2\") format(\"woff2\"), url(\"../fonts/nexa-heavy-webfont.woff\") format(\"woff\");\n  font-weight: normal;\n  font-style: normal; }\n\n/* ------------------------------------ *\\\n    $FORMS\n\\* ------------------------------------ */\nform {\n  font-family: \"nexa_book\", \"Arial\", \"Helvetica\", sans-serif;\n  font-family: \"nexa_book\", \"Arial\", \"Helvetica\", sans-serif;\n  font-size: 1rem;\n  line-height: 1.5; }\n\nform ol,\nform ul {\n  list-style: none;\n  margin-left: 0; }\n\nlegend {\n  margin-bottom: 0.375rem;\n  font-weight: bold; }\n\nfieldset {\n  border: 0;\n  padding: 0;\n  margin: 0;\n  min-width: 0; }\n\nbutton,\ninput,\nselect,\ntextarea {\n  font-family: inherit;\n  font-size: 100%; }\n\ninput,\nselect,\ntextarea {\n  width: 100%;\n  border: 1px solid #e0d8d6;\n  padding: 20px;\n  -webkit-appearance: none;\n  border-radius: 0.1875rem;\n  outline: 0; }\n\ninput[type=\"checkbox\"],\ninput[type=\"radio\"] {\n  width: auto;\n  margin-right: 0.3em; }\n\ninput[type=\"search\"] {\n  -webkit-appearance: none;\n  border-radius: 0; }\n\ninput[type=\"search\"]::-webkit-search-cancel-button,\ninput[type=\"search\"]::-webkit-search-decoration {\n  -webkit-appearance: none; }\n\n::placeholder {\n  color: #adadad; }\n\n/**\n * Validation\n */\n.has-error {\n  border-color: #f00 !important; }\n\n.is-valid {\n  border-color: #089e00 !important; }\n\n.c-form label {\n  margin-bottom: 10px;\n  display: block; }\n\n.c-form__fields {\n  display: flex;\n  flex-wrap: wrap;\n  margin-left: calc($space/2 * -1);\n  margin-right: calc($space/2 * -1); }\n  .c-form__fields-item {\n    padding: 0 10px;\n    margin-bottom: 20px;\n    width: 100%; }\n    .c-form__fields-item--half {\n      width: 50%; }\n    .c-form__fields-item--qaurter {\n      width: 25%; }\n\n/* ------------------------------------ *\\\n    $HEADINGS\n\\* ------------------------------------ */\nh1,\n.o-heading--xxl {\n  font-family: \"signature_collection_alt\";\n  font-size: var(--font-size-xxl);\n  line-height: 1;\n  font-weight: normal; }\n\nh2,\n.o-heading--xl {\n  font-family: \"silver_south_serif\", \"Times New Roman\", serif;\n  font-size: var(--font-size-xl);\n  line-height: 1.15;\n  letter-spacing: 0.1em; }\n\nh3,\n.o-heading--l {\n  font-family: \"silver_south_serif\", \"Times New Roman\", serif;\n  font-size: var(--font-size-l);\n  line-height: 1.25;\n  letter-spacing: 0.1em; }\n\nh4,\n.o-heading--m {\n  font-family: \"silver_south_serif\", \"Times New Roman\", serif;\n  font-size: var(--font-size-m);\n  line-height: 1.35;\n  letter-spacing: 0.05em; }\n\nh5,\n.o-heading--s {\n  font-family: \"nexa_book\", \"Arial\", \"Helvetica\", sans-serif;\n  font-size: var(--font-size-s);\n  letter-spacing: 0.1em; }\n\nh6,\n.o-heading--xs {\n  font-family: \"nexa_book\", \"Arial\", \"Helvetica\", sans-serif;\n  font-size: var(--font-size-xs);\n  letter-spacing: 0.1em; }\n\n/* ------------------------------------ *\\\n    $LINKS\n\\* ------------------------------------ */\na {\n  text-decoration: none;\n  color: #554b47;\n  transition: all 0.25s ease-in-out;\n  display: inline-block; }\n\n.u-link--underline {\n  text-decoration: underline; }\n  .u-link--underline:hover {\n    text-decoration: none; }\n\n/* ------------------------------------ *\\\n    $LISTS\n\\* ------------------------------------ */\n